import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError } from 'rxjs';
import { environment } from '../../../environments/environment';
import { ResponseFL } from '../models/responseFL';
import { catchError, map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class ProductUploadFileService {


  private urlValidateFile: string = `${environment.rutas.validateFile}`;
  private urlGenerateFile: string = `${environment.rutas.generateFile}`;

  message: string;

  nextStepper: boolean = false;
  previosStepper: boolean = false;
  invalidFile: boolean = false;
  deleteFile: boolean = false;
  disableNavItem: boolean = false;
  sendFiles: boolean = false;
  validateSpinners: boolean;
  files: ResponseFL[] = [];
  client: string;
  filesInvalid: ResponseFL[] = [];
  registrosSolicitadosCliente: string;

  constructor(
    private httpClient: HttpClient
  ) { }

  validateFile(file) {

    const formData = new FormData();
    formData.append('file', file);
    formData.append('startTime', new Date().getTime().toString());
    return this.httpClient.post<any>(this.urlValidateFile, formData)
      .pipe(
        catchError(this.error)
      )
  }

  generateFiles() {

    let formData = new FormData();
    for (let i = 0; i < this.files.length; i++) {
      if (this.files[i].file) {
        formData.append('files', this.files[i].file);
      }
    }
    formData.append('client', this.client);
    return this.httpClient.post<any>(this.urlGenerateFile, formData)
      .pipe(
        catchError(this.error)
      )
  }

  validateFilesSpiners(): boolean {

    let files = this.files.filter(file => file.spinner);
    if (files.length > 0) {
      this.invalidFile = true;
      return false
    } else {
      this.nextStepper = true;
      this.invalidFile = false;
      return true;
    }
  }

  error(error: HttpErrorResponse) {
    return throwError(error)
  }
}
