import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ProductUploadFileService } from '../../core/services/product-upload-file.service';
import { ModalDialogService } from '../../core/services/modal-dialog.service';
import { environment } from '../../../environments/environment';
import { Router } from '@angular/router';
import { TooltipPosition } from '@angular/material/tooltip';
import { CrauServiceService } from '../../core/services/crau-service.service';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  positionOptions: TooltipPosition[] = ['below', 'above', 'left', 'right'];
  position = new FormControl(this.positionOptions[0]);
  private sso: string = `${environment.rutas.sso}`;

  constructor(
    public productUploadService: ProductUploadFileService,
    private modalDialogService: ModalDialogService,
    private router: Router,

  ) { }

  ngOnInit(): void {

  }


  disableNavItem() {

    this.productUploadService.disableNavItem = true;


  }

  enableNavItem() {

    this.router.navigate(['producto/listar']);

  }

  logout() {
    this.productUploadService.invalidFile = false;
    this.modalDialogService.showDialog('En caso de continuar se cerrara la sesión', 'logout');
  }
}
