import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { CrauServiceService } from '../services/crau-service.service';

@Injectable({
  providedIn: 'root'
})
export class InterceptorService implements HttpInterceptor {

  constructor(
    private crauService: CrauServiceService

  ) { }
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {


    const skip = req.params.get('skip');
    if (skip === 'yes') {
      const skip = req.params.delete('skip');
      const reqClone = req.clone({ params: skip });
      return next.handle(reqClone).pipe(
        catchError(this.error)
      );
    } else {
      const headers = new HttpHeaders({ 'Authorization': `Bearer ${this.crauService.token}` });
      const reqClone = req.clone({ headers });
      return next.handle(reqClone).pipe(
        catchError(this.error)
      )
    }
  }

  error(error: HttpErrorResponse) {

    return throwError(error);

  }
}
