import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProductUploadFileService } from '../../core/services/product-upload-file.service';
//import { ModalDialogService } from '../../../services/modal-dialog.service';



@Component({
  selector: 'app-confirm-dialog',
  templateUrl: './confirm-dialog.component.html',
  styleUrls: ['./confirm-dialog.component.css']
})
export class ConfirmDialogComponent implements OnInit {

  filesInvalidate = [];
  component: string;


  constructor(
    public uploadFileService: ProductUploadFileService,
    public dialogRef: MatDialogRef<ConfirmDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public message: string = "Mensaje") { }


  ngOnInit() {

    this.filesInvalidate = this.uploadFileService.filesInvalid;


  }

  onClick() {
    this.dialogRef.close(true);
  }

  


}
