import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { client } from '../../../assets/helpers/const';
import { map, catchError } from 'rxjs/operators';
import { of, throwError } from 'rxjs';
import { ResponseFLJ } from '../models/responseFLJ';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/x-www-form-urlencoded'
  })
}

@Injectable({
  providedIn: 'root'
})
export class CrauServiceService {

  private sso: string = `${environment.rutas.sso}`;
  private auth: string = `${environment.rutas.auth}`;
  private refresh: string = `${environment.rutas.refresh}`;
  private urlFlow: string = `${environment.rutas.flow}`;


  token: string;
  client_id: string;
  client_secret: string;

  constructor(
    private http: HttpClient
  ) {
    this.token = '';
    this.client_id = client.cliente_id
    this.client_secret = client.client_secret;

  }

  getAccesToken(jti: string) {

    const formData = new FormData();
    formData.append('jti', jti);
    return this.http.post(this.auth, formData, { params: { skip: 'yes' } }).pipe(
      map((resp: any) => {
        this.saveStorage(resp.accesToken, resp.refreshToken, resp.userId);
        return true;
      }),
      catchError(error => {
        return of(false)
      })
    )
  }

  renewToken() { 

    const formData = new FormData();
    formData.append('refresh_token', sessionStorage.getItem('refreshToken') || ' ');
    formData.append('client_id', this.client_id);
    formData.append('client_secret', this.client_secret)

    return this.http.post(this.refresh, formData, { params: { skip: 'yes' } }).pipe(
      map((resp: any) => {
        this.saveStorage(resp.access_token, resp.refresh_token, resp.sub);
        return true;
      }),
      catchError(error => {
        return of(false)
      })
    )


  }

  saveStorage(token: string, refreshToken: string, userId) {

    this.token = token;
    sessionStorage.setItem('refreshToken', refreshToken);
    sessionStorage.setItem('idUsuario', userId);
  }

  saveFlow(_body: ResponseFLJ) {

    return this.http.post(this.urlFlow, _body, httpOptions)
      .pipe(
        catchError(this.error)
      )
  }

  deleteStorage() {
    this.token = '';
    sessionStorage.removeItem('refreshToken');
    sessionStorage.removeItem('idUsuario');
  }

  error(error: HttpErrorResponse) {
    return throwError(error)
  }

}
